.TodoTemplate {
    width: 512px;
    // width가 주어진 상태에서 좌우 중앙 정렬
    margin-left: auto;
    margin-right: auto;
    margin-top: 6rem;
    border-radius: 4px;
    overflow: hidden;
    
    .app-title {
        background: #C5E0B4;
        color: #4e7920;
        height: 4rem;
        font-size: 1.5rem;
        font-weight: bold;
        border: 1px solid #4e7920;
        display: flex; // Flexbox Froggy(http://flexboxfroggy.com/#ko) 참고
        align-items: center;
        justify-content: center;
    }
    .content {
        background: #fff;
    }
}