.TodoInsert {
    display: flex;
    background: #C5E0B4;
    color: #4e7920;
    input {
        // 기본 스타일 초기화
        background: none;
        outline: none;
        border: none;
        border: 1px solid #4e7920;
        padding: 0.5rem;
        font-size: 1.125rem;
        line-height: 1.5;
        color: #4e7920;
        &::placeholder {
            color: #4e7920;
        }
        // 버튼을 제외한 영역을 모두 차지하기
        flex: 1;
    }

    button {
        // 기본 스타일 초기화
        background: none;
        outline: none;
        border: none;
        background: #92D050;
        color: white;
        padding-left: 1rem;
        padding-right: 1rem;
        font-size: 1.5rem;
        display: flex;
        align-items: center;
        border: 1px solid #4e7920;
        cursor: pointer;
        transition: 0.1s background ease-in; // 서서히 변하게 하는 속성
        &:hover {
            background: #82c936;
        }
    }
}