.TodoListItem {
    padding: 1rem;
    display: flex;
    align-items: center; // 세로 중앙 정렬
    border: 1px solid #4e7920;
    &:nth-child(even) {
        background: #E2F0D9;
    }
    .checkbox {
        cursor: pointer;
        flex: 1; // 차지할 수 있는 영역 모두 차지
        display: flex;
        align-items: center ; // 세로 중앙 정렬
        svg {
            // 아이콘
            color: #000000;
            font-size: 1.5rem; // -> size는 안 되는 이유?
            
        }
        .text {
            margin-left: 0.5rem;
            flex: 1; // 차지할 수 있는 영역 모두 차지
        }
        // 체크되었을 때 보여 줄 스타일
        &.checked {
            svg {
                color: #92D050;
            }
            .text {
                color: #92D050;
                text-decoration: line-through;
            }
        }
    }
    .remove {
        display: flex;
        align-items: center;
        color: #ff6b6b;
        cursor: pointer;
        &:hover {
            color: #ff8787;
        }
    }

    // 엘리먼트 사이사이에 테두리를 넣어 줌
    & + & {
        border-top: 1px solid #92D050;
    }
}